.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
  border-radius: 50%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-top: 16vmin;
  padding-bottom: 16vmin;
}

.App-link {
  color: #61dafb;
}


/* 未被访问的样式 */
a:link {
  color: #99FFFF ;
  text-decoration: none;
}

/* 已被点击后的样式 */
a:visited {
  color: #99FFFF;
  text-decoration: none;
}

/* 鼠标悬停的样式 */
a:hover {
  color: #FFCC22;
  text-decoration: none;
}

/* 已被激活的样式 */
a:active {
  color: #FFCC22;
  text-decoration: none;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
